import Bridge from "suplink-sdk/bridge";
import { Return } from "./interface";

export default function login(): Promise<Return> {
  return Bridge.dispatchAppEvent("reqGetOpenId").then((res) => {
    let { data } = res;
    return {
      code: res.code,
      data: {
        token: data.token,
        userName: data.requestUser,
        appId: data.appId,
        baseUrl: data.baseUrl,
        appName: data.minappName,
        supostoken: data.supostoken,
      },
      msg: res.msg,
    };
  });
}
