import { isFunc } from "../../utils";

import { EventChannel } from "suplink-sdk";
export default function offAppShow(cb: (...params: any[]) => any) {
  if (isFunc(cb)) {
    let name = cb.name;
    if (name) {
      EventChannel.__events = EventChannel.__events.filter((event) => event["offAppShow"] && event["offAppShow"].name !== name);
    }
  }
}
