import Bridge from "suplink-sdk/bridge";
import { Return, Params } from "./interface";

export default function personnelSelector(param: Params): Promise<Return> {
  let { title = "人员选择", type = 1, max, selected = [] } = param;

  if (!Array.isArray(selected)) {
    selected = [];
  }
  return Bridge.dispatchAppEvent("sl.contact.selector", {
    title,
    type,
    maxLimit: max,
    selected: selected.map((staffCode) => ({ staffCode })),
  });
}
