import Bridge from "suplink-sdk/bridge";
import { Return, Params } from "./interface";

export default function departSelect(param: Params): Promise<Return> {
  let { title = "部门选择", max, selected = [] } = param;

  if (!Array.isArray(selected)) {
    selected = [];
  }
  return Bridge.dispatchAppEvent("sl.contact.departmentSelector", {
    title,
    maxLimit: max,
    selected: selected.map((uuid) => ({ uuid })),
  });
}
