import Bridge from "suplink-sdk/bridge";
import { Return } from "./interface";

export default function getStorage(key: string): Promise<Return> {
  return Bridge.dispatchAppEvent("sl.storage.json.get", { key }).then((res) => {
    let data
    try {
      data = JSON.parse(res.data)
    } catch (err) {
      data = res.data
    }
    return Promise.resolve({
      ...res,
      data
    })
  })
}
