import { isFunc, uuid } from "../../utils";
import { EventChannel } from "suplink-sdk";
export default function onAppShow(cb: Function & { _uuid?: string }) {
  if (isFunc(cb)) {
    const _uuid = uuid();
    cb._uuid = _uuid;
    if (!Array.isArray(EventChannel.__events)) {
      EventChannel.__events = [];
    }

    EventChannel.__events.push({
      onAppShow: cb,
      key: _uuid,
    });
  }
}
