import Bridge from "suplink-sdk/bridge";
import { Return } from "./interface";

export default function setStorage(key:string, value:any = ''): Promise<Return> {
  let val
  try {
    val = JSON.stringify(value)
  } catch (err) {
    val = value
  }
  return Bridge.dispatchAppEvent("sl.storage.json.set", {
    key,
    value: val
  });
}
