/* eslint-disable */

function isAndroid() {
  var ua = typeof window !== "undefined" ? navigator.userAgent.toLowerCase() : "android";
  var isA = ua.indexOf("android") > -1;
  if (isA) {
    return true;
  }
  return false;
}

function setup_Android_WebViewJavascriptBridge() {
  //@ts-ignore
  if (typeof window !== "undefined" && window.WebViewJavascriptBridge) {
    return;
  } //@ts-ignore
  var messagingIframe: HTMLElement;
  //@ts-ignore
  var bizMessagingIframe: HTMLElement;
  //@ts-ignore
  var sendMessageQueue: any[] = [];
  //@ts-ignore
  var receiveMessageQueue: any[] = [];
  var messageHandlers: any = {};

  var CUSTOM_PROTOCOL_SCHEME = "yy"; //'yy';
  var QUEUE_HAS_MESSAGE = "__bridge_loaded__"; //  __QUEUE_MESSAGE__/

  var responseCallbacks: any = {};
  var uniqueId = 1;

  // 创建消息index队列iframe
  function _createQueueReadyIframe(doc: Document) {
    messagingIframe = doc.createElement("iframe");
    messagingIframe.style.display = "none";
    doc.documentElement.appendChild(messagingIframe);
  }

  //创建消息体队列iframe
  function _createQueueReadyIframe4biz(doc: any) {
    bizMessagingIframe = doc.createElement("iframe");
    bizMessagingIframe.style.display = "none";
    doc.documentElement.appendChild(bizMessagingIframe);
  }

  //set default messageHandler  初始化默认的消息线程
  function init(messageHandler: any) {
    //@ts-ignore
    if (typeof window !== "undefined" && window.WebViewJavascriptBridge._messageHandler) {
      throw new Error("WebViewJavascriptBridge.init called twice");
    }
    if (typeof window !== "undefined") {
      //@ts-ignore
      window.WebViewJavascriptBridge._messageHandler = messageHandler;
      //@ts-ignore
      var receivedMessages = receiveMessageQueue;
      //@ts-ignore
      receiveMessageQueue = null;
      for (var i = 0; i < receivedMessages.length; i++) {
        _dispatchMessageFromNative(receivedMessages[i]);
      }
    }
  }

  // 发送
  function send(data: any, responseCallback: any) {
    _doSend(
      {
        data: data,
      },
      responseCallback
    );
  }

  // 注册线程 往数组里面添加值
  function registerHandler(handlerName: any, handler: any) {
    //@ts-ignore
    messageHandlers[handlerName] = handler;
  }

  // 调用线程
  function callHandler(handlerName: any, data: any, responseCallback: any) {
    _doSend(
      {
        handlerName: handlerName,
        data: data,
      },
      responseCallback
    );
  }

  //sendMessage add message, 触发native处理 sendMessage
  function _doSend(message: any, responseCallback: any) {
    if (responseCallback) {
      var callbackId = "cb_" + uniqueId++ + "_" + new Date().getTime();
      //@ts-ignore
      responseCallbacks[callbackId] = responseCallback;
      message.callbackId = callbackId;
    }
    sendMessageQueue.push(message);
    //@ts-ignore
    messagingIframe.src = CUSTOM_PROTOCOL_SCHEME + "://" + QUEUE_HAS_MESSAGE;
  }

  // 提供给native调用,该函数作用:获取sendMessageQueue返回给native,由于android不能直接获取返回的内容,所以使用url shouldOverrideUrlLoading 的方式返回内容
  function _fetchQueue() {
    //@ts-ignore
    var messageQueueString = JSON.stringify(sendMessageQueue);
    sendMessageQueue = [];
    //@ts-ignore
    bizMessagingIframe.src = CUSTOM_PROTOCOL_SCHEME + "://return/_fetchQueue/" + encodeURIComponent(messageQueueString);

    //android can't read directly the return data, so we can reload iframe src to communicate with java
    // bizMessagingIframe.src = CUSTOM_PROTOCOL_SCHEME + '://return/_fetchQueue/' + encodeURIComponent(messageQueueString);
  }

  //提供给native使用,
  function _dispatchMessageFromNative(messageJSON: any) {
    setTimeout(function () {
      var message = JSON.parse(messageJSON);
      var responseCallback;
      //java call finished, now need to call js callback function
      if (message.responseId) {
        //@ts-ignore
        responseCallback = responseCallbacks[message.responseId];
        if (!responseCallback) {
          return;
        }
        responseCallback(message.responseData);
        //@ts-ignore
        delete responseCallbacks[message.responseId];
      } else {
        //直接发送
        if (message.callbackId) {
          var callbackResponseId = message.callbackId;
          responseCallback = function (responseData: any) {
            //@ts-ignore
            _doSend({
              responseId: callbackResponseId,
              responseData: responseData,
            });
          };
        }

        if (typeof window !== "undefined") {
          //@ts-ignore
          var handler = window.WebViewJavascriptBridge._messageHandler;
          if (message.handlerName) {
            //@ts-ignore
            handler = messageHandlers[message.handlerName];
          }
          //查找指定handler
          try {
            handler(message.data, responseCallback);
          } catch (exception) {
            if (typeof console != "undefined") {
            }
          }
        }
      }
    });
  }

  //提供给native调用,receiveMessageQueue 在会在页面加载完后赋值为null,所以
  //@ts-ignore
  function _handleMessageFromNative(messageJSON) {
    //@ts-ignore
    if (receiveMessageQueue) {
      receiveMessageQueue.push(messageJSON);
    }
    _dispatchMessageFromNative(messageJSON);
  }

  if (typeof window !== "undefined") {
    //@ts-ignore
    var WebViewJavascriptBridge = (window.WebViewJavascriptBridge = {
      init: init,
      send: send,
      registerHandler: registerHandler,
      callHandler: callHandler,
      _fetchQueue: _fetchQueue,
      _handleMessageFromNative: _handleMessageFromNative,
    });
  }

  _createQueueReadyIframe(window.document);
  _createQueueReadyIframe4biz(window.document);
  // _createQueueuReadyIframeForIOS(doc);
  //@ts-ignore
  var readyEvent = window.document.createEvent("Events");
  readyEvent.initEvent("WebViewJavascriptBridgeReady");
  //@ts-ignore
  readyEvent.bridge = WebViewJavascriptBridge;
  //@ts-ignore
  window.document.dispatchEvent(readyEvent);
}

// ================= 这里是Android注入配置 =================
// ================= 这里是Android注入配置 =================
// ================= 这里是Android注入配置 =================

// ================= 这里是iOS注入配置 =================
// ================= 这里是iOS注入配置 =================
// ================= 这里是iOS注入配置 =================
//@ts-ignore
function setupWebViewJavascriptBridge(callback: any) {
  //@ts-ignore
  if (typeof window !== "undefined" && window.WebViewJavascriptBridge) {
    //@ts-ignore
    return callback(window.WebViewJavascriptBridge);
  }
  //@ts-ignore
  if (typeof window !== "undefined" && window.WVJBCallbacks) {
    console.log("callback存到WVJBCallbacks");
    //@ts-ignore
    return window.WVJBCallbacks.push(callback);
  }

  if (typeof window !== "undefined") {
    //@ts-ignore
    window.WVJBCallbacks = [callback];
    var WVJBIframe = window.document.createElement("iframe");
    WVJBIframe.style.display = "none";
    WVJBIframe.src = "https://__bridge_loaded__";
    window.document.documentElement.appendChild(WVJBIframe);
    setTimeout(function () {
      window.document.documentElement.removeChild(WVJBIframe);
    }, 0);
  }
}

function callhandler(name: string, data: any, callback: any) {
  setupWebViewJavascriptBridge(function (bridge: any) {
    bridge.callHandler(name, data, callback);
  });
}

function registerhandler(name: string, callback: any) {
  setupWebViewJavascriptBridge(function (bridge: any) {
    bridge.registerHandler(name, function (data: any, responseCallback: any) {
      callback(data, responseCallback);
    });
  });
}

export const dispatchAppEvent = (
  eventName: string,
  data: any = {}
): Promise<{
  code: any;
  data: any;
  msg: string;
}> => {
  if (!isAndroid()) {
    return new Promise((resolve, reject) => {
      callhandler(eventName, JSON.stringify(data), (res: any) => {
        try {
          const resData = JSON.parse(res);
          if (resData.responseCode === "200") {
            resolve({
              code: resData.responseCode,
              msg: resData.msg,
              data: resData.responseResult,
            });
          } else {
            reject(resData);
          }
        } catch (error) {
          reject(error);
        }
      });
    });
  }

  return new Promise((resolve, reject) => {
    if (
      typeof window !== "undefined" &&
      //@ts-ignore
      window.WebViewJavascriptBridge &&
      //@ts-ignore
      window.WebViewJavascriptBridge.callHandler
    ) {
      //@ts-ignore
      console.log("dispatchAppEvent");
      //@ts-ignore
      window.WebViewJavascriptBridge.callHandler(eventName, JSON.stringify(data), (res: any) => {
        console.log("dispatchAppEvent - callback");
        try {
          const resData = JSON.parse(res);
          if (parseInt(resData.responseCode, 10) === 200) {
            resolve({
              code: resData.responseCode,
              msg: resData.msg,
              data: resData.responseResult,
            });
          } else {
            reject(resData);
          }
        } catch (error) {
          reject(error);
        }
      });
    } else {
      console.log("dispatchAppEvent - fail");
      reject(new Error("window.WebViewJavascriptBridge.callHandler is undefined"));
    }
  });
};

(function () {
  if (typeof window !== "undefined") {
    if (isAndroid()) {
      setup_Android_WebViewJavascriptBridge();
    } else {
      setupWebViewJavascriptBridge(function (_bridge: any) {});
    }
  }
})();

//  为了防止supos报错
export default {
  callHandler: callhandler,
  registerHandler: registerhandler,
  dispatchAppEvent,  // 天访里叫 zhizhiDispatchAppEvent
};
