import React, { useEffect, useState } from "react";
import { Table } from "antd";
import { useDocs, Link } from "docz";
import lodash from "lodash";
type Props = {
  data: any[];
  columns?: any[];
  require?: boolean;
};

export default function DocTable(props: Props) {
  const [data, setData] = useState<any[]>([]);

  const columns = props.columns
    ? props.columns
    : props.require
    ? //@ts-ignore
      [
        {
          title: "属性",
          dataIndex: "property",
          key: "property",
        },
        {
          title: "类型",
          dataIndex: "type",
          key: "type",
        },
        {
          title: "说明",
          dataIndex: "desc",
          key: "desc",
          render: desc => <div dangerouslySetInnerHTML={{__html:desc}}></div>
        },
        {
          title: "默认值",
          dataIndex: "default",
          key: "default",
          width: 100,
        },
      ].concat({
        title: "必填",
        dataIndex: "require",
        key: "require",
        width: 100,
      })
    : [
        {
          title: "属性",
          dataIndex: "property",
          key: "property",
        },
        {
          title: "类型",
          dataIndex: "type",
          key: "type",
        },
        {
          title: "说明",
          dataIndex: "desc",
          key: "desc",
        },
        {
          title: "默认值",
          dataIndex: "default",
          key: "default",
          width: 100,
        },
      ];
  useEffect(() => {
    setData(
      props.data.map((d: any, i: number) => {
        d.key = i;
        return d;
      })
    );
  }, [props.data]);

  return (
    <div style={{ padding: "20px 0" }}>
      <Table size="small" dataSource={data} pagination={false} columns={columns} />
    </div>
  );
}

export function HomeTable() {
  const docs = useDocs()
    .filter((item) => item.name !== "快速上手")
    .map((item) => {
      return {
        ...item,
        and: "支持",
        ios: "支持",
      };
    });
  const rows = {};
  docs.forEach((doc) => {
    //@ts-ignore
    rows[doc.group] = rows[doc.group] || [];
    //@ts-ignore
    rows[doc.group].push(doc);
  });

  const newDocs = lodash.flattenDeep(Object.values(rows));

  const columns = [
    {
      title: "分类",
      dataIndex: "group",
      key: "group",
    },
    {
      title: "名称",
      dataIndex: "method",
      key: "method",
      render: (value, row) => {
        return <Link to={row.route}>{value}</Link>;
      },
    },
    {
      title: "说明",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "Android",
      dataIndex: "and",
      key: "and",
    },
    {
      title: "iOS",
      dataIndex: "ios",
      key: "ios",
    },
  ];

  return <DocTable columns={columns} data={newDocs}></DocTable>;
}
