import { uuid } from "../utils";

type TypeEvent = {
  type?: "once" | "always" | "done";
  key: string;
  [eventName: string]: any;
};

export class Events {
  private static instance: Events;
  __events: TypeEvent[];
  constructor() {
    this.__events = [];
  }
  static getInstance = () => {
    if (!Events.instance) {
      Events.instance = new Events();
    }
    return Events.instance;
  };
  on = (eventName: string, fn: Function & { _uuid?: string }) => {
    const _uuid = uuid();
    fn._uuid = _uuid;
    Events.instance.__events.push({
      [eventName]: fn,
      type: "always",
      key: _uuid,
    });
  };
  //监听一个事件一次，触发后失效
  once = (eventName: string, fn: Function & { _uuid?: string }) => {
    const _uuid = uuid();
    fn._uuid = _uuid;
    Events.instance.__events.push({
      type: "once",
      [eventName]: fn,
      key: _uuid,
    });
  };
  //取消监听一个事件。给出第二个参数时，只取消给出的监听函数，否则取消所有监听函数
  off = (eventName: string, EventCallback?: Function & { _uuid?: string }) => {
    if (typeof EventCallback === "function") {
      if (EventCallback._uuid) {
        Events.instance.__events = Events.instance.__events.filter((event) => !(event[eventName] && event.key === EventCallback._uuid));
      }
    } else {
      Events.instance.__events = Events.instance.__events.filter((event) => !event[eventName]);
    }
  };
  //
  emit = (eventName: string) => {
    Events.instance.__events
      .filter((event) => {
        return event[eventName];
      })
      .forEach((event) => {
        //  有可能有些函数使用到了this， 但是又已经销毁
        try {
          if (event.type === "done") {
          } else {
            event[eventName]();
            if (event.type === "once") {
              event.type = "done";
            }
          }
        } catch (error) {
          console.log(error);
        }
      });
    Events.instance.__events = Events.instance.__events.filter((event) => event.type !== "done");
  };
}

export default Events.getInstance();
