import Bridge from "suplink-sdk/bridge";
import { Return } from "./interface";

export default function getUserInfo(): Promise<Return> {
  return Bridge.dispatchAppEvent("sl.contact.userInfo").then((res) => {
    let userInfo = res.data.userInfo || {};
    return {
      code: res.code,
      msg: res.msg,
      data: {
        ...res.data,
        ...userInfo,
      },
    };
  });
}
