import Bridge from "suplink-sdk/bridge";
import { ReturnNavbarBack, Params } from "./interface";

export default function navBack(param: Params): Promise<ReturnNavbarBack> {
  const { toast: backActionAlertEnabled = false, title: backActionAlertTitle = "" } = param;
  return Bridge.dispatchAppEvent("sl.config.navigation.back", {
    backActionAlertEnabled,
    backActionAlertTitle,
  });
}
