import Bridge from "suplink-sdk/bridge";
import { Return, Params } from "./interface";

export default function navConfig(param: Params): Promise<Return> {
  const {
    title: toolbarTitle,
    background: toolbarBackColor = "#ffffff",
    backBtn: isShowBackBtn = true,
    theme: toolbarMode = "dark",
    goast: isFloat = false,
    hidden: isHideToolBar = false,
    FunctionButton = true,
    underline = true,
  } = param;
  return Bridge.dispatchAppEvent("sl.config.viewInterface", {
    toolbarTitle,
    toolbarBackColor,
    isShowBackBtn,
    toolbarMode,
    isFloat,
    isHideToolBar,
    isHideFunctionButton: !!!FunctionButton,
    isHideBottomLine: !underline,
  });
}
